.contact-us{
    width: 100%;
    height: 100vh;
    position: relative;
}
.contact-bkg{
    height: 100vh;
    width: 100%;
    z-index: -1;
   
    position: absolute;
    img{
    
        width: 100%;
        height: 100%;
    
    }
}
.contact-container{
align-items: center;
    width: 100%;
    height:100vh;
    
    margin: auto auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
        

}
.title-contact{

display: flex;

    h1{
        margin: 5px 35px;
        color: $OrangeColor;
        font-weight: 900;
        font-size: 2.5rem;

    }
}
.contact-content{
   display: flex;
   
   width: 100%;
   height: 100%;
   justify-content: space-around;

   .contact-image{
    
    height: 100%;

    img{width: 500px;
    height: 100%;}
}
}
.spins{
    display: flex;
    padding: 5px 10px;
    justify-content: space-between;
    width: 100%;
    height: 10px;

  align-items:center;
    
}
.spin{
    width: 15px;
    height: 15px;
    border: 2px solid $OrangeColor;
    border-radius: 50px;
    background-color: $OrangeColor;
}
.form-container-contact{
    
    display: flex;
    flex-direction: column;
  
    height: 100%;
    width: 100%;
}
.form-contact-title{
    display: flex;
  
    width: 100%;
    align-content: center;
    align-items: center;
    align-self: start;
margin-left: 10px;
    .paper-image{
        width: 20%;
        height: 50px;
        img{width: 100%;
        height: 100%;}
    }
    h1{
        margin: 0 20px;
        color: $VioletColor;
        font-size: 40px;
    }
}

.btn-contactUs{
    display: flex;
    justify-content: center;
    margin: 0 auto;
 

}